.header {
  width: 69%;
  max-width: 880px;
  border-bottom: 1px solid rgba(84,84,84,0.7);
  font-size: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 320px) {
  .header {
    width: 100%;
  }
}
