.header__link {
  font-family: 'Inter';
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  color: #fff;
  text-decoration: none;
  margin-top: 10px;
}

.header__link:hover {
  opacity: .6;
  transition: opacity .1s ease; 
}

@media (max-width: 400px) {
  .header__link {
    font-size: 14px;
  }
}